import React from "react";
import getHomePageData from "../../data/home";
import SectionCard from "./components/section-card";
import checkmark from "../../images/icon-large-checkmark.png";
import "./home.less";
import { useState } from "react";

const Home: React.FC = () => {
	const [selectedFont, setSelectedFont] = useState('Open Sans');

	// List of available fonts
	const fonts = [
		'Open Sans',
		'Roboto',
		'Lato',
		'Source Sans Pro',
		'Merriweather',
		'Inter',
		'Work Sans',
		'Montserrat',
		'Raleway',
		'Poppins',
	];

	// Function to handle font change
	const handleFontChange = (event: any) => {
		const selected = event.target.value;
		setSelectedFont(selected);

		// Apply the selected font to the body
		document.body.style.fontFamily = selected;
	};



	const { WhyInvestWithUs } = getHomePageData();

	return (
		<div className="home-container">

			<div>
				<label htmlFor="font-selector">Choose a font: </label>
				<select id="font-selector" onChange={handleFontChange} value={selectedFont}>
					{fonts.map((font, index) => (
						<option key={index} value={font}>
							{font}
						</option>
					))}
				</select>
			</div>

			<div className="why-invest">
				<h2 className="title">{WhyInvestWithUs.Title}</h2>
				{WhyInvestWithUs.Sections.map((section, index) => (
					<SectionCard
						key={index}
						title={section.Title}
						content={section.ContentText}
						logo={checkmark}
					/>
				))}
			</div>

		</div>
	);
};

export default Home;